.wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 50px;
  background-color: var(--primary);
  padding: 4px 0;
  gap: 16px;
  .logo-icon {
    display: flex;
    height: 100%;
    padding: 4px;
    background-color: var(--text-gold-color);
    border-radius: 8px;
    img {
      height: 100%;
    }
  }
  .logo-text {
    display: flex;
    height: 100%;
    img {
      height: 100%;
    }
  }
}
